"use client";

import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Progress } from "@/components/ui/progress";

interface Submission {
  applicationId: string;
  fullName: string;
  email: string;
  phone: string;
  itemDescription: string;
  estimatedValue: number;
  address: string;
  photoUrl: string;
  status: "pending" | "accepted" | "rejected";
  submittedAt: string;
  reviewedAt: string | null;
  reviewedBy: string | null;
  pawnValue: number | null;
  pickupScheduled: string | null;
  fundsDisbursed: boolean;
  repaymentDue: string | null;
  itemReturned: boolean;
  rejectionReason?: string;
}

export default function StatusPage() {
  const searchParams = useSearchParams();
  const [applicationId, setApplicationId] = useState(searchParams.get("id") || "");
  const [submission, setSubmission] = useState<Submission | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    if (searchParams.get("id")) {
      handleSearch();
    }
  }, []);

  const handleSearch = async () => {
    if (!applicationId.trim()) {
      setError("ID Aplikasi harus diisi");
      return;
    }

    setLoading(true);
    setError("");
    setSubmission(null);

    try {
      const response = await fetch(`/api/user/status?id=${encodeURIComponent(applicationId.trim())}`);
      
      if (!response.ok) {
        if (response.status === 404) {
          throw new Error("Pengajuan dengan ID tersebut tidak ditemukan");
        }
        throw new Error("Gagal mengambil status pengajuan");
      }

      const data = await response.json();
      setSubmission(data.submission);

    } catch (err) {
      setError(err instanceof Error ? err.message : "Terjadi kesalahan");
    } finally {
      setLoading(false);
    }
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("id-ID", {
      style: "currency",
      currency: "IDR",
      minimumFractionDigits: 0,
    }).format(amount);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString("id-ID", {
      year: "numeric",
      month: "long",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "pending":
        return <Badge variant="secondary">Menunggu Review</Badge>;
      case "accepted":
        return <Badge className="bg-green-100 text-green-800 hover:bg-green-100">Diterima</Badge>;
      case "rejected":
        return <Badge variant="destructive">Ditolak</Badge>;
      default:
        return <Badge variant="outline">{status}</Badge>;
    }
  };

  const getProgressValue = () => {
    if (!submission) return 0;
    
    switch (submission.status) {
      case "pending":
        return 25;
      case "rejected":
        return 100;
      case "accepted":
        if (submission.itemReturned) return 100;
        if (submission.fundsDisbursed) return 75;
        if (submission.pickupScheduled) return 50;
        return 40;
      default:
        return 0;
    }
  };

  const getStatusSteps = () => {
    if (!submission) return [];

    const steps = [
      {
        title: "Pengajuan Diterima",
        description: `Pengajuan Anda telah diterima pada ${formatDate(submission.submittedAt)}`,
        completed: true,
        current: false,
      },
    ];

    if (submission.status === "rejected") {
      steps.push({
        title: "Pengajuan Ditolak",
        description: `Pengajuan ditolak: ${submission.rejectionReason || "Tidak memenuhi kriteria"}`,
        completed: true,
        current: false,
      });
    } else if (submission.status === "accepted") {
      steps.push(
        {
          title: "Review Selesai",
          description: `Pengajuan disetujui dengan nilai gadai ${formatCurrency(submission.pawnValue || 0)}`,
          completed: true,
          current: false,
        },
        {
          title: "Penjemputan Dijadwalkan",
          description: submission.pickupScheduled 
            ? `Penjemputan dijadwalkan pada ${formatDate(submission.pickupScheduled)}`
            : "Menunggu penjadwalan penjemputan",
          completed: !!submission.pickupScheduled,
          current: !submission.pickupScheduled && !submission.fundsDisbursed,
        },
        {
          title: "Dana Dicairkan",
          description: submission.fundsDisbursed 
            ? "Dana telah dicairkan ke rekening Anda"
            : "Menunggu pencairan dana",
          completed: submission.fundsDisbursed,
          current: !!submission.pickupScheduled && !submission.fundsDisbursed,
        },
        {
          title: "Masa Pelunasan",
          description: submission.repaymentDue 
            ? `Batas pelunasan: ${formatDate(submission.repaymentDue)}`
            : "Menunggu informasi pelunasan",
          completed: submission.itemReturned,
          current: submission.fundsDisbursed && !submission.itemReturned,
        }
      );

      if (submission.itemReturned) {
        steps.push({
          title: "Barang Dikembalikan",
          description: "Barang telah dikembalikan kepada Anda",
          completed: true,
          current: false,
        });
      }
    } else {
      steps.push({
        title: "Menunggu Review",
        description: "Pengajuan Anda sedang ditinjau oleh tim kami",
        completed: false,
        current: true,
      });
    }

    return steps;
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white">
      {/* Header */}
      <header className="border-b bg-white/80 backdrop-blur-sm">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <Link href="/" className="text-2xl font-bold text-gray-900">
              GadaiKu
            </Link>
            <nav className="flex gap-4">
              <Link href="/submit">
                <Button variant="outline">Ajukan Gadai</Button>
              </Link>
              <Link href="/admin">
                <Button variant="outline">Admin</Button>
              </Link>
            </nav>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <div className="container mx-auto px-4 py-8">
        <div className="max-w-4xl mx-auto">
          <div className="mb-8">
            <h1 className="text-3xl font-bold text-gray-900 mb-2">Cek Status Pengajuan</h1>
            <p className="text-gray-600">Masukkan ID aplikasi untuk melihat status pengajuan gadai Anda</p>
          </div>

          {/* Search Form */}
          <Card className="mb-8">
            <CardHeader>
              <CardTitle>Cari Pengajuan</CardTitle>
              <CardDescription>
                Masukkan ID aplikasi yang Anda terima saat mengajukan gadai
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="flex gap-4">
                <div className="flex-1">
                  <Label htmlFor="applicationId">ID Aplikasi</Label>
                  <Input
                    id="applicationId"
                    type="text"
                    value={applicationId}
                    onChange={(e) => setApplicationId(e.target.value)}
                    placeholder="Contoh: APP000001"
                    className="mt-1"
                  />
                </div>
                <div className="flex items-end">
                  <Button 
                    onClick={handleSearch}
                    disabled={loading}
                  >
                    {loading ? "Mencari..." : "Cari"}
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>

          {error && (
            <Alert className="mb-6 border-red-200 bg-red-50">
              <AlertDescription className="text-red-800">
                {error}
              </AlertDescription>
            </Alert>
          )}

          {/* Status Display */}
          {submission && (
            <div className="space-y-6">
              {/* Status Overview */}
              <Card>
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <div>
                      <CardTitle>Status Pengajuan</CardTitle>
                      <CardDescription>ID: {submission.applicationId}</CardDescription>
                    </div>
                    {getStatusBadge(submission.status)}
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    <div>
                      <div className="flex justify-between text-sm mb-2">
                        <span>Progress</span>
                        <span>{getProgressValue()}%</span>
                      </div>
                      <Progress value={getProgressValue()} className="h-2" />
                    </div>
                    
                    <div className="grid md:grid-cols-2 gap-4 text-sm">
                      <div>
                        <p><strong>Nama:</strong> {submission.fullName}</p>
                        <p><strong>Barang:</strong> {submission.itemDescription}</p>
                        <p><strong>Estimasi Nilai:</strong> {formatCurrency(submission.estimatedValue)}</p>
                      </div>
                      <div>
                        <p><strong>Tanggal Pengajuan:</strong> {formatDate(submission.submittedAt)}</p>
                        {submission.pawnValue && (
                          <p><strong>Nilai Gadai:</strong> {formatCurrency(submission.pawnValue)}</p>
                        )}
                        {submission.repaymentDue && (
                          <p><strong>Batas Pelunasan:</strong> {formatDate(submission.repaymentDue)}</p>
                        )}
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Timeline */}
              <Card>
                <CardHeader>
                  <CardTitle>Timeline Proses</CardTitle>
                  <CardDescription>
                    Ikuti perkembangan pengajuan gadai Anda
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    {getStatusSteps().map((step, index) => (
                      <div key={index} className="flex gap-4">
                        <div className="flex flex-col items-center">
                          <div className={`w-4 h-4 rounded-full border-2 ${
                            step.completed 
                              ? "bg-green-500 border-green-500" 
                              : step.current 
                                ? "bg-blue-500 border-blue-500" 
                                : "bg-gray-200 border-gray-300"
                          }`} />
                          {index < getStatusSteps().length - 1 && (
                            <div className={`w-0.5 h-8 ${
                              step.completed ? "bg-green-500" : "bg-gray-200"
                            }`} />
                          )}
                        </div>
                        <div className="flex-1 pb-4">
                          <h3 className={`font-semibold ${
                            step.current ? "text-blue-600" : step.completed ? "text-green-600" : "text-gray-500"
                          }`}>
                            {step.title}
                          </h3>
                          <p className="text-sm text-gray-600 mt-1">
                            {step.description}
                          </p>
                        </div>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>

              {/* Payment Section */}
              {submission.status === "accepted" && submission.fundsDisbursed && !submission.itemReturned && (
                <Card>
                  <CardHeader>
                    <CardTitle>Pelunasan</CardTitle>
                    <CardDescription>
                      Lakukan pelunasan untuk mengambil barang Anda
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-4">
                      <div className="p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
                        <p className="text-yellow-800">
                          <strong>Jumlah yang harus dibayar:</strong> {formatCurrency((submission.pawnValue || 0) * 1.1)} 
                          <span className="text-sm ml-2">(termasuk bunga 10%)</span>
                        </p>
                        {submission.repaymentDue && (
                          <p className="text-yellow-800 text-sm mt-1">
                            Batas waktu: {formatDate(submission.repaymentDue)}
                          </p>
                        )}
                      </div>
                      <Link href={`/payment?id=${submission.applicationId}`}>
                        <Button className="w-full">
                          Bayar Sekarang
                        </Button>
                      </Link>
                    </div>
                  </CardContent>
                </Card>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
